.photos-wrapper {
  padding: 30px;
  margin: 30px 0;
}

.photos {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: 300px 200px 200px 200px 200px;
  gap: 10px;

  .photo-item {
    overflow: hidden;
    border: 2px solid #fff;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      background: rgba(255, 0, 0, 0.4);
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }

    &:first-of-type {
      grid-column: 2 / span 5;
    }

    &:nth-of-type(2) {
      grid-column: 7 / span 3;
    }

    &:nth-of-type(3) {
      grid-column: 1 / span 3;
    }

    &:nth-of-type(4) {
      grid-column: 4 / span 4;
    }

    &:nth-of-type(5) {
      grid-column: 3 / span 4;
      img {
        object-position: center;
      }
    }

    &:nth-of-type(6) {
      grid-column: 7 / span 3;
    }

    &:nth-of-type(7) {
      grid-column: 1 / span 3;
    }

    &:nth-of-type(8) {
      grid-column: 4 / span 4;
    }

    &:nth-of-type(9) {
      grid-column: 7 / span 3;
    }

    img {
      transition: all 0.3s ease;
      object-fit: cover;
      object-position: top;
      height: 100%;

      &:hover {
        transform: scale(0.95);
      }
    }
  }
}
